import { classManagerApi } from "./classManagerApi";
import {
  classTabControllerFunction,
  classTabManageFunction,
} from "./class_manager_query_mutation_function";

const classManagerApiDynamic = classManagerApi.injectEndpoints({
  endpoints: (builder) => ({
    classTabManage: classTabManageFunction(builder),
    classTabController: classTabControllerFunction(builder),
  }),
});
export const { useClassTabManageQuery, useClassTabControllerMutation } =
  classManagerApiDynamic;
